import React, { useContext } from 'react'
import { Link } from 'react-router-dom'
import { Context } from '../context/context'

const Index = () => {
  // context
  const { onFetch } = useContext(Context)

  const btnClickmeClick = async () => {
    let data = await onFetch('/test.php', 'GET')
    console.log('data final:', data)
  }

  return (
    <>
      <h3>React.version: {React.version}</h3>
      <Link to='/about'>ABOUT PAGE</Link><br /><br />
      <button onClick={btnClickmeClick}>Click me</button>
    </>
  )
}

export default Index