import { createContext } from "react";

const ENDPOINT = 'http://gucitex.com.mx/test'
const API_NO_CORS = 'https://cors-anywhere.herokuapp.com/'
const TOKEN_DEFAULT = 'DEFAULT'

async function processPromise(prom) {
    let data = await prom.then(res => res.text())
    .then(txt => {
        console.log('resTxt:', txt)
        return JSON.parse(txt)
    })
    .catch(error => {
        console.error('error fetch:', error)
        return null
    })

    return data
}

const onFetch = async (params, method = 'GET', body = null, token = TOKEN_DEFAULT) => {
    if (body !== null) body = JSON.stringify(body)
    console.log('body:', body)
    console.log('ENDPOINT + PARAMS:', API_NO_CORS + ENDPOINT + params)
    const prom = fetch(API_NO_CORS + ENDPOINT + params, {
        method: method,
        headers: {
            'Content-Type': 'application/json',
            'Origin': '',
            Authorization: token
        },
        body: body
    })
    
    return await processPromise(prom)
}

export const Context = createContext({
    onFetch
})